import {
	request
} from "../../utils/request.js"

// 
export const getHomeInfo = (params) =>
	request({
		url: '/messages/home/get',
		method: 'get',
		params
	})
// 
export const getHomeData = (params) =>
	request({
		url: '/tasks/home/get',
		method: 'get',
		params
	})
// 
export const getExpressage = (params) =>
	request({
		url: `/tasks/${params.type}/${params.state}`,
		method: 'get',
		params
	})
// 
// 
