import {
	request
} from "../../utils/request.js"
// 
export const getUserInfo = (params) =>
	request({
		url: `/user/profile`,
		method: 'get',
		params
	})
// 
export const getRealNameStatusApi = (params) =>
	request({
		url: `/user/realNameVerify`,
		method: 'get',
		params
	})
//
export const updateUserInfo = (params) =>
	request({
		url: `/user/profile`,
		method: 'put',
		params
	})
