import {
	request
} from "../../utils/request.js"

// 
export const phoneLogins = (params) =>
	request({
		url: `/logins/phone`,
		method: 'post',
		params
	})
// 
export const userLogins = (params) =>
	request({
		url: `/login/account`,
		method: 'post',
		params
	})
// 
export const getsmsCode = (params) =>
	request({
		url: `/verifyCodes/smsCode`,
		method: 'post',
		params
	})
// 
export const getUserInfo = (params) =>
	request({
		url: `/users/get`,
		method: 'get',
		params
	})