import {
	baseUrl
} from './env'
//  url:  param  method callBack
export function request({
	url = '',
	params = {},
	method = 'GET'
}) {
	// uni.getStorage({
	// 	key: ''
	// })
	// token
	const token = uni.getStorageSync('token')
	let header = {
		// 'Accept': 'application/json',
		'Access-Control-Allow-Origin': '*',
		'Content-Type': 'application/json;charset=UTF-8',
		'Authorization': token
	}
	const requestRes = new Promise((resolve, reject) => {
		uni.request({
			url: baseUrl + url,
			data: params,
			header: header,
			method: method,
			success: (res) => {
				const {
					data
				} = res
				if (data.code == 0 || data.code == 200) {
					resolve(res.data)
					// console.log(res.data,1)
				} else {
					resolve(res.data)
					handleError(res,resolve)
					// console.log(res.data,2)
				}
				console.log(res,'res')
			},
			fail: (err) => {
				// console.log(err,'err')
				const error = {
					data: {
						msg: err.data
					}
				}
				
				// handleError(err)
				reject(error)
			}

		})
	})
	const handleError = (error,resolve) => {
		var errorCode = error.statusCode;
		if (errorCode == 401) {
			uni.showToast({
				title: "",
				icon: "none",
				duration: 1000,
				success:()=>{
					setTimeout(()=>{
						uni.navigateTo({
							url: '/pages/login/index'
						});
					},2000)
				
				},
				fail:()=>{
					console.log(777)
				}
			});
		} else if (errorCode == 500) {
			if (uni.getStorageSync("token") == "") {
				uni.showToast({
					title: "",
					icon: "none",
					duration: 2000,
					success:()=>{
						console.log(6666)
					},
					fail:()=>{
						console.log(777)
					}
				});
			} else {
				uni.showToast({
					title: error.data.error.message + "",
					icon: "none",
					duration: 3000
				});
			}
		} else {
			resolve(error)
			// uni.showToast({
			// 	title: "",
			// 	icon: "none",
			// 	duration: 1000
			// });
		}
	}
	return requestRes
}
